.op3-top-menu {
  position: relative;
  background-color: $color__op3-white;
  height: 65px;
  border-bottom: 1px solid $color__op3-borderLight;
  box-sizing: border-box;

  .op3-logo {
    display: inline-block;
    height: 35px;

    i {
      font-size: 35px;
      line-height: 35px;
    }
  }

  ul {
    margin: 0;
    padding: 0;

    li {
      margin: 0;
      padding: 0;
      position: relative;

      a {
        display: inline-block;
        text-decoration: none;
        font-size: 20px;
        line-height: 20px;
      }
    }
  }

  .op3-right {
    nav {
      display: inline-block;
    }

    ul {
      line-height: 65px;
      text-align: center;
      display: inline-block;

      &.op3-right-menu {
        li {
          display: inline-block;
          margin-right: 40px;
          a {
            display: block;
            color: $color__op3-blue;
            position: relative;
            line-height: 65px;
            transition: $transition__color-linear;

            i {
              font-size: 25px;
              line-height: 25px;
              height: 25px;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
            }
            span {
              font-size: 14px;
              padding-left: 35px;
            }
          }

          &:hover,
          &.selected {
            a {
              color: $color__op3-black;
            }
          }
        }
      }

      &.op3-right-static {
        li {
          border-left: 1px solid $color__op3-borderLight;

          &.op3-fullscreenBtn {
            a {
              i {
                transition: $transition__color-linear;
              }
              &:hover {
                i {
                  color: $color__op3-gray;
                }
              }
            }
          }

          a {
            width: 80px;
            line-height: 65px;
          }
        }
      }
    }
  }
}


