.op3_movingBorderMenu {
  position: relative;

  .op3_movingBorderMenuHover {
    width: 0;
    height: 3px;
    display: block;
    background-color: $color__op3-logoBlue;
    transition: $transition__width-left-linear;
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
  }
}


