.op3-button {
  display: inline-block;
  position: relative;
  line-height: 45px;
  text-align: center;
  min-width: 300px;
  border-radius: 5px;
  color: $color__op3-white;
  text-decoration: none !important;
  font-size: 13px;
  font-weight: 600;
  transition: $transition__color-bg-color-linear;
  @include linear-gradient(to top right, #0175FF, #0061D4);

  &:hover {
    color: $color__op3-white;
    @include linear-gradient(to top right, #0061D4, #0061D4);
  }

  &.op3-min-width-auto {
    min-width: auto;
  }

  &.op3-full-width {
    min-width: auto;
    width: 100%;
  }

  &.op3-transparent {
    background: none;
    color: $color__op3-logoBlue;
    border: 1px solid $color__op3-borderLight;

    &:hover {
      background: $color__op3-logoBlue;
      color: $color__op3-white;
    }
  }

  &.op3-green {
    background: $color__op3-green;
  }

  &.op3-red {
    background: $color__op3-red;
  }
}


