.op3-form {
  label {
    margin-bottom: 5px;
    color: $color__op3-black;
  }

  input {
    width: 100%;
    display: block;
    border: 1px solid $color__op3-borderLight;
    border-radius: 5px;
    line-height: 40px;
    font-size: 16px;
    box-sizing: border-box;
    padding: 0 5px;
    margin-bottom: 20px;
    &:focus {
      box-shadow: none;
      border-color: initial;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &::placeholder {
      opacity: 0.4;
    }
  }

  select {
    border: 1px solid $color__op3-borderLight;
    &:focus {
      box-shadow: none;
      border-color: initial;
    }
  }

  button {
    &:focus {
      box-shadow: none;
      border-color: initial;
      outline: none;
    }
  }

  button[type="submit"] {
    margin-top: 15px;
  }

  @import "form/onoffswitch";
}


