.op3-top-menu {
  position: relative;
  background-color: #FFFFFF;
  height: 65px;
  border-bottom: 1px solid #f1f1f1;
  box-sizing: border-box;
}

.op3-top-menu .op3-logo {
  display: inline-block;
  height: 35px;
}

.op3-top-menu .op3-logo i {
  font-size: 35px;
  line-height: 35px;
}

.op3-top-menu ul {
  margin: 0;
  padding: 0;
}

.op3-top-menu ul li {
  margin: 0;
  padding: 0;
  position: relative;
}

.op3-top-menu ul li a {
  display: inline-block;
  text-decoration: none;
  font-size: 20px;
  line-height: 20px;
}

.op3-top-menu .op3-right nav {
  display: inline-block;
}

.op3-top-menu .op3-right ul {
  line-height: 65px;
  text-align: center;
  display: inline-block;
}

.op3-top-menu .op3-right ul.op3-right-menu li {
  display: inline-block;
  margin-right: 40px;
}

.op3-top-menu .op3-right ul.op3-right-menu li a {
  display: block;
  color: #86939E;
  position: relative;
  line-height: 65px;
  transition: color 0.3s linear;
}

.op3-top-menu .op3-right ul.op3-right-menu li a i {
  font-size: 25px;
  line-height: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.op3-top-menu .op3-right ul.op3-right-menu li a span {
  font-size: 14px;
  padding-left: 35px;
}

.op3-top-menu .op3-right ul.op3-right-menu li:hover a,
.op3-top-menu .op3-right ul.op3-right-menu li.selected a {
  color: #000000;
}

.op3-top-menu .op3-right ul.op3-right-static li {
  border-left: 1px solid #f1f1f1;
}

.op3-top-menu .op3-right ul.op3-right-static li.op3-fullscreenBtn a i {
  transition: color 0.3s linear;
}

.op3-top-menu .op3-right ul.op3-right-static li.op3-fullscreenBtn a:hover i {
  color: #858585;
}

.op3-top-menu .op3-right ul.op3-right-static li a {
  width: 80px;
  line-height: 65px;
}

.op3-button {
  display: inline-block;
  position: relative;
  line-height: 45px;
  text-align: center;
  min-width: 300px;
  border-radius: 5px;
  color: #FFFFFF;
  text-decoration: none !important;
  font-size: 13px;
  font-weight: 600;
  transition: color 0.3s linear, background 0.3s linear;
  background: #0175FF;
  background: -webkit-linear-gradient(bottom left, #0175FF, #0061D4);
  background: linear-gradient(to top right, #0175FF, #0061D4);
}

.op3-button:hover {
  color: #FFFFFF;
  background: #0061D4;
  background: -webkit-linear-gradient(bottom left, #0061D4, #0061D4);
  background: linear-gradient(to top right, #0061D4, #0061D4);
}

.op3-button.op3-min-width-auto {
  min-width: auto;
}

.op3-button.op3-full-width {
  min-width: auto;
  width: 100%;
}

.op3-button.op3-transparent {
  background: none;
  color: #0174fc;
  border: 1px solid #f1f1f1;
}

.op3-button.op3-transparent:hover {
  background: #0174fc;
  color: #FFFFFF;
}

.op3-button.op3-green {
  background: #00d88a;
}

.op3-button.op3-red {
  background: #f87457;
}

.op3-white-box {
