.op3_movingBorderMenu {
  position: relative;
}

.op3_movingBorderMenu .op3_movingBorderMenuHover {
  width: 0;
  height: 3px;
  display: block;
  background-color: #0174fc;
  transition: width 0.3s ease, left 0.3s ease;
  position: absolute;
  bottom: 0;
  left: 0;
  pointer-events: none;
}

.op3-form {
  /**
<div class="op3-onoffswitch">
    <input type="checkbox" name="onoffswitch" class="op3-onoffswitch-checkbox" id="myonoffswitch" checked>
    <label class="op3-onoffswitch-label" for="myonoffswitch">
        <span class="op3-onoffswitch-inner"></span>
        <span class="op3-onoffswitch-switch"></span>
    </label>
</div>
 */
}

.op3-form label {
  margin-bottom: 5px;
  color: #000000;
}

.op3-form input {
  width: 100%;
  display: block;
  border: 1px solid #f1f1f1;
  border-radius: 5px;
  line-height: 40px;
  font-size: 16px;
  box-sizing: border-box;
  padding: 0 5px;
  margin-bottom: 20px;
}

.op3-form input:focus {
  box-shadow: none;
  border-color: initial;
}

.op3-form input:last-child {
  margin-bottom: 0;
}

.op3-form input::placeholder {
  opacity: 0.4;
}

.op3-form select {
  border: 1px solid #f1f1f1;
}

.op3-form select:focus {
  box-shadow: none;
  border-color: initial;
}

.op3-form button:focus {
  box-shadow: none;
  border-color: initial;
  outline: none;
}

.op3-form button[type="submit"] {
  margin-top: 15px;
}

.op3-form .op3-onoffswitch {
  position: relative;
  width: 73px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.op3-form .op3-onoffswitch-checkbox {
  display: none;
}

.op3-form .op3-onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid #e5e9ef;
  border-radius: 20px;
}

.op3-form .op3-onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  transition: margin 0.3s ease-in 0s;
}

.op3-form .op3-onoffswitch-inner:before,
.op3-form .op3-onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  height: 30px;
  padding: 0;
  line-height: 30px;
  font-size: 14px;
  color: #FFFFFF;
  box-sizing: border-box;
}

.op3-form .op3-onoffswitch-inner:before {
  content: "ON";
  padding-left: 10px;
  background-color: #0174fc;
  color: #FFFFFF;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.op3-form .op3-onoffswitch-inner:after {
  content: "OFF";
  padding-right: 10px;
  background-color: #FFFFFF;
  color: #858585;
  text-align: right;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.op3-form .op3-onoffswitch-switch {
  display: block;
  width: 10px;
  height: 10px;
  margin: 10px;
  background: #FFFFFF;
  position: absolute;
  top: 3px;
  bottom: 0;
  right: 39px;
  border: 2px solid #e5e9ef;
  border-radius: 20px;
  transition: all 0.3s ease-in 0s;
}

.op3-form .op3-onoffswitch-checkbox:checked + .op3-onoffswitch-label .op3-onoffswitch-inner {
  margin-left: 0;
}

.op3-form .op3-onoffswitch-checkbox:checked + .op3-onoffswitch-label .op3-onoffswitch-switch {
  right: 0px;
}

.op3-top-menu a {
  outline: none;
}

.op3-top-menu a:focus {
  box-shadow: none;
}

.op3-secondary-menu {
  margin: 40px auto;
}

.op3-secondary-menu nav {
  display: inline-block;
}

.op3-secondary-menu nav ul {
  border-bottom: 1px solid #e5e9ef;
  margin-bottom: 0;
}

.op3-secondary-menu nav ul li {
  display: inline-block;
  margin: 0 15px;
}

.op3-secondary-menu nav ul li:first-child {
