}

.op3-secondary-menu nav ul li:last-child {
  margin-right: 0;
}

.op3-secondary-menu nav ul li a {
  padding: 25px 0;
  color: #858585;
  text-decoration: none;
  display: inline-block;
}

.op3-secondary-menu nav ul li a:hover {
  color: #0174fc;
}

.op3-secondary-menu nav ul li.selected a {
  color: #0174fc;
}

.op3-secondary-menu .op3_movingBorderMenuHover {
  bottom: -1px;
}




