.op3-white-box {
  background-color: $color__op3-white;
  box-shadow: 0px 4px 17px 0px rgba(56,79,134,0.1);
  margin: 0 auto 25px;
  padding: 50px;
  border-radius: 5px;
  position: relative;

  &:last-child {
    margin-bottom: 0;
  }

  &.op3-danger {
    border-top:5px solid #ff0000;
  }

  &.op3-two-column {
    .d-flex {
      @include wp-mobile() {
        flex-wrap: wrap;
      }
      div {
        @include wp-mobile() {
          width: 100%;
        }
        &:last-child {
          padding-left: 60px;
          margin-left: 60px;
          border-left: 1px solid $color__op3-borderLight;

          @include wp-mobile() {
            padding-left: 0;
            margin-left: 0;
            border-left: none;
          }
        }
      }
    }
  }
}


