/**
<div class="op3-onoffswitch">
    <input type="checkbox" name="onoffswitch" class="op3-onoffswitch-checkbox" id="myonoffswitch" checked>
    <label class="op3-onoffswitch-label" for="myonoffswitch">
        <span class="op3-onoffswitch-inner"></span>
        <span class="op3-onoffswitch-switch"></span>
    </label>
</div>
 */
.op3-onoffswitch {
  position: relative;
  width: 73px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.op3-onoffswitch-checkbox {
  display: none;
}

.op3-onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 2px solid $color__op3-borderBlue;
  border-radius: 20px;
}

.op3-onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  transition: margin 0.3s ease-in 0s;
}

.op3-onoffswitch-inner:before, .op3-onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  height: 30px;
  padding: 0;
  line-height: 30px;
  font-size: 14px;
  color: $color__op3-white;
  box-sizing: border-box;
}

.op3-onoffswitch-inner:before {
  content: "ON";
  padding-left: 10px;
  background-color: $color__op3-logoBlue;
  color: $color__op3-white;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.op3-onoffswitch-inner:after {
  content: "OFF";
  padding-right: 10px;
  background-color: $color__op3-white;
  color: $color__op3-gray;
  text-align: right;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}

.op3-onoffswitch-switch {
  display: block;
  width: 10px;
  height: 10px;
  margin: 10px;
  background: $color__op3-white;
  position: absolute;
  top: 3px;
  bottom: 0;
  right: 39px;
  border: 2px solid $color__op3-borderBlue;
  border-radius: 20px;
  transition: all 0.3s ease-in 0s;
}

.op3-onoffswitch-checkbox:checked + .op3-onoffswitch-label .op3-onoffswitch-inner {
  margin-left: 0;
}

.op3-onoffswitch-checkbox:checked + .op3-onoffswitch-label .op3-onoffswitch-switch {
  right: 0px;
}


